<script lang="coffeescript">

  # Project Show

  import {push, pop, replace, location} from 'svelte-spa-router'




  
  # Data Item

  
  import { debug, FlexRow, FlexRowCell, InlineInput } from "../../Braintapper"
  import { RootItem, PageContent, PageHeader, QuickEntry, DueDatePopover, ProgressPopover, ListMenu, ListDroppableDivider } from "../../Application"

  debug.on = window.debug
  


  # Common Layout Components



  # Misc Components



  # Sections

  import ListItems from './_ListItems.svelte'



  # Local vars

  export params = {}
  export list = undefined


  # --------------------------------------------------------------------------------------------------------------------

  resource = new RootItem


  
  isDragging = false # global state

  setDragging = ()->
    console.log "Show - dragging"
    isDragging = true

  unsetDragging = ()->
    console.log "Show - stop dragging"
    isDragging = false

  getList = ()-> # get list from db and refresh local view
    scb = (doc) ->
      list = doc
      console.log list
    resource.get(params.id, scb, null, null)

  refreshAndUpdate = ()->
    list = list # svelte refresh
    unsetDragging()
    deferredUpdate getList

  update = (scb, ecb, fcb)-> # calling directly breks scope, so call within update fn
    resource.update scb, ecb, null

  deferredUpdate = update.debounce(500)


  cloneRootItem = (e)->
    
    
    console.error "cloneRootItem"
    # original = new RootItem(doc.clone)
    scb = (d)->
      console.error "cloneRootItem scb"
      console.debug d
    list.clone(scb)
    # console.table clone
  
    



  # CRUD EVENTS


  # update root list attribute
  updateAttribute = (e)->

    Object.merge(list, e.detail)
    console.error "updateAttribute"
    console.debug list
    refreshAndUpdate()

  # Child Events

  updateChildItem = (e)-> # update a child in the list_items hierarchy
    console.error "updateChildItem"
    console.log e.detail
    change = e.detail
    originalItemReference = list.reference_from_ancestry(change.ancestry)
    originalItemIndex = originalItemReference.list_items.findIndex({uuid: change.uuid})
    originalItem = originalItemReference.list_items[originalItemIndex]

    Object.merge(originalItem, change)
    refreshAndUpdate()

  addListItem = (event)-> # add to this item only, not to children
    resource.add_list_item event.detail, null, refreshAndUpdate

  destroyListItem = (e)->
    resource.delete_list_item e.detail, refreshAndUpdate




  cloneChildItem = (e, scb)->
    console.error "LISTITEM SHOW CLONE CHILD ITEM"
    console.log e.detail
    console.log "-----"

    resource.cloneListItem e.detail, refreshAndUpdate

  # List

  destroyList = (e)->
    console.error "DESOTRY LIST"
    scb = ()->
      push "/lists"
    resource.destroy list.id, scb


  updateRootItem = (e)->
    ecb = (err)->
      # refreshList()
    resourceList.patch e.detail, refreshList, ecb, null

  #

  # Drag Drop



  topDropHandler = (event)->
    console.error "DROP ON ROOT LIST"
    console.debug event.detail
    origin = event.detail.dragItem
    destination = resource
    # event.detail.dropItem.inderx
    # index = event.detail.dropItem.index

    destination["to_index"] = event.detail.dropItem.index


    console.error "DESTINATION"
    console.log destination


    ecb = (e)->
      console.log "drophandler error"
      console.log e

    resource.drop_item origin, destination, refreshAndUpdate, ecb



  dividerDropHandler = (event)->
    console.error "dividerDropHandler"

  nestHandler = (event)->

    origin = event.detail.dragItem
    destination = event.detail.dropItem


    ecb = (e)->
      console.log "nesthandler error"
      console.log e

    resource.nest_item origin, destination, refreshAndUpdate, ecb




  dropHandler = (event)->
    console.error "SHOW dropHandler"
    console.debug event.detail
    origin = event.detail.dragItem
    destination = event.detail.dropItem

    ecb = (e)->
      console.log "drophandler error"
      console.log e

    resource.drop_item origin, destination, refreshAndUpdate, ecb

  


  addHandler = (event)->
    console.error "SHOW addHandler"
    console.log event.detail
    resource.insert_item { uuid: uuid(), name: event.detail.name || "New item created at #{Date.create().format('{HH}:{mm}:{ss}')}" }, event.detail, refreshAndUpdate

  # Reactive

  `$: title = list ? list.name : "Unnamed list"`

  # handle a reroute
  `$: if (params.id) { getList() }`



</script>

<svelte:head>
  <title>Lists :: {title}</title>
</svelte:head>
{#if list}

  <PageContent>

    
    <PageHeader icon={true}>
      <div slot="icon"><ProgressPopover bind:value={list.progress} item={list} on:update={updateAttribute} size={36}/></div>
      <div>
        <FlexRow style="max-width: 100%;">
          <FlexRowCell flex style="overflow-x: hidden">
            <h1 title>
            <InlineInput bind:value={list.name} on:update={deferredUpdate} style="margin-left: -2px" inputStyle="margin-top: 2px; margin-left: -1px"/>
            </h1>
          </FlexRowCell>
          <FlexRowCell style="padding-top: 16px; width: auto; margin-right: 8px" flex="initial">
            <DueDatePopover item={list} value={list.due_date} on:update={updateAttribute} on:open on:close isListItem={false} idAttribute="id"/>
          </FlexRowCell>        
          <FlexRowCell style="padding-top: 16px; width: 32px" flex="initial">
            <ListMenu bind:item={list} on:select={updateAttribute} on:clone={cloneRootItem} on:destroy={destroyList}/>
          </FlexRowCell>        
        </FlexRow>
      </div>
    </PageHeader>

    
    <hr/>
    
    
    <ListDroppableDivider item={{ list_items: [],ancestry: [], index: -1} } on:drop={dropHandler} on:add={addHandler} bind:dragState={isDragging}/>
    <ListItems bind:list={list} bind:dragState={isDragging} on:clone={cloneChildItem} on:update={updateChildItem} on:drop={dropHandler} on:dividerDrop={dividerDropHandler} on:nest={nestHandler} on:add={addHandler} on:destroy={destroyListItem} on:dragging={setDragging} on:dragStop={unsetDragging}/>

    <ListDroppableDivider item={{ list_items: [], ancestry: [], index: list.list_items.length + 1 }} bind:dragState={isDragging} on:drop={dropHandler} on:add={addHandler}/>
    {#if list.is_archived == false}
    <QuickEntry on:add={addListItem} stubPlaceholder="Add Top Level List Item" />
    {/if}

  
  </PageContent>
  
{/if}
<style lang="sass">

  h6
    text-transform: uppercase
    margin-bottom: -4px
    color: var(--gray)
    letter-spacing: 0.75px

  hr
    margin-bottom: 36px

</style>
