<script lang="coffeescript">
  import { createEventDispatcher } from "svelte"
  dispatch = createEventDispatcher()


  import Menu from './_List.svelte'

  import { 
    ButtonIconPopover,
    IconSvg,
    ModalDeleteConfirmation 
  } from '../../../Braintapper'

  export item = undefined

  showDeleteConfirmation = false

  popoverEvents = 
    "selected": (e)->
      handleMenuSelection e.detail

  handleMenuSelection = (selection)->
    switch selection #}
      when "favorite"
        console.log "SELECTION #{selection}"
        dispatch "select", { _id: item._id, is_favorite: ! item.is_favorite }
      when "archive"
        dispatch "select", { _id: item._id, is_archived: ! item.is_archived }
      when "delete"
        showDeleteConfirmation = true
      when "clone"
        dispatch "clone", { _id: item._id }
      else
        console.log "do nothing"


  deleteConfirmationCancel = ()->
    showDeleteConfirmation = false

  destroy = ()->
    dispatch "destroy", item
    showDeleteConfirmation = false



</script>

<ButtonIconPopover popover={Menu} events={popoverEvents}>
  <IconSvg classes="menu" icon="menu"/>
</ButtonIconPopover>

{#if showDeleteConfirmation}
  <ModalDeleteConfirmation on:destroy={destroy} on:cancel={deleteConfirmationCancel}/>
{/if}
