<script lang="coffeescript">

  import { createEventDispatcher } from 'svelte'
  dispatch = createEventDispatcher()

  import tippy from 'tippy.js'

  
  export popover = undefined  # svelte template for the popover

  export options = {} # options to pass to the popover
  export style = undefined
  export classes = undefined
  export disabled = false

  isShowing = false

  export events = {}

  

  ###

    events =
      "key": (e)->
      ...

  ###



  tippyObject = undefined
  component = undefined

  # todo:
  # fill
  #
  #
  

  tippyPopover = (element, content) ->

    tippyOptions =
      theme: "menu"
      appendTo: "parent"
      trigger: "click"
      placement: "bottom-end"
      interactive: true
      popperOptions:
        modifiers: [
          {
            name: "flip"
            options:
              fallbackPlacements: ["top","right","left"]
          }

        ]

    if (typeof content == 'function')

      tippyOptions.onCreate = (instance) ->
        component = new content(target: instance.popper.querySelector('.tippy-content'))
        component.$set { options: options }
        #console.log "tipy set optoins = "
        #console.log options
        Object.keys(events).forEach (eventKey)->
          component.$on eventKey, (e)->
            tippyObject.hide()
            events[eventKey](e)

        return

      tippyOptions.onHide = (instance) ->
        #console.error "TIPPY HIDE"
        isShowing = false
        component.$set { isShowing: isShowing }

      tippyOptions.onShow = (instance) ->
        #console.error "TIPPY SHOW"
        isShowing = true
        component.$set { isShowing: isShowing }

    else
      tippyOptions.content = content


    if Object.keys(tippyOptions).length == 0
      tippyObject = tippy(element)
    else
      tippyObject = tippy(element, tippyOptions)
    return
  
      {
        update: (params) ->
          console.error "update"
          # if component
          #  component.$set { value: selectedDate }

          return
        destroy: ->
          tippyObject.destroy()
          if component
            # debugger
            component.$destroy()
          return

      }

  updatedOptions = (newOptions)->
    if component?
      #console.log "tipy updatedOptions optoins = "
      #console.log options
      component.$set { options: newOptions }
      
  `$: updatedOptions(options)`

</script>

<button type="button" on:click {style} {disabled} use:tippyPopover={popover} class={classes}><slot/></button>

<style lang="sass">
  button 
    display: inline-block
    padding: 0px
    background: none
    height: 24px
    min-width: 24px
    width: 24px
    border: none
    margin: 0px
    &:hover 
      background: none
</style>