<script lang="coffeescript">
  # /lists
  # List Index


  import {push, pop, replace, location} from "svelte-spa-router"


  # Data Items

  import { 
    debug,
    ButtonIcon,
    FlexRow,
    FlexRowCell, 
    IconToggleSvg,
    InlineInput 
  } from "../../Braintapper"


  import { 
    ListHoverableContainer,
    RootItemList,
    RootItem,
    ProgressPopover,
    DueDatePopover,
    QuickEntry,
    ListMenu
  } from "../../Application"


  #

  # Local Vars

  export list = []
  

  export options = {}

  export context = "current"

  

  # List Model

  resourceList = new RootItemList

  refreshList = (items)->
    list = items






  # jumps

  itemUrl = "#/lists/" # links and redirects


  goToItem = (item)->
    push "#/lists/#{item._id}"

  toggleFavorite = (rootItemList)->
    #console.log "toggle Favorite"
    #console.log rootItemList

    rootItemList.is_favorite = !rootItemList.is_favorite

    list = list

    updateRootItem
      detail:
        id: rootItemList._id
        is_favorite: rootItemList.is_favorite

  # CRUD


  cloneRootItem = (e)->
    resource = new RootItem
    scb = (doc) ->
      
      
      clone = resource.clone()
      
    
    resource.get(e.detail._id, scb, null, null)


  addRootItem = (e)->
    scb = (inserted)->
      push "/lists/#{inserted._id}" # redirect to list page
    resourceList.insert e.detail, scb, null, null


  selectAndRefresh = (view)->
    switch view
      when "archived"
        resourceList.selectArchived refreshList
      else # "Lists"
        resourceList.select refreshList
        
  

  updateRootItem = (e)->


    ecb = (err)->
      # refreshList()
    resourceList.patch e.detail, selectAndRefresh, ecb, null

  destroy = (e)->
    ecb = (e)->
      console.log "destroy error"
      console.log e

    resourceList.destroy e.detail._id, selectAndRefresh, ecb


  # UI

  processList = (list, options)->


    sortableList = list.map (o)->
      # because due_date can be null, and because 
      # sortBy has issues with nulls
      # need to create a sort field

      if options.sort_by == "due_date"
        # always keep nulls at the bottom
        if options.sort_descending
          surrogateDate = "0000-00-00"
        else
          surrogateDate = "9999-99-99"
        o["sort_field"] = o[options.sort_by] || surrogateDate
      else
        o["sort_field"] = o.progress
      return o

    filteredList = sortableList.filter (listItem)->


      result = true
      if options.favorites_only
        result = result && listItem.is_favorite


      if !options.include_completed
        result = result && (listItem.progress.percent_complete < 100)

      return result

    if list?
      return filteredList.sortBy "sort_field", options.sort_descending
        

    else
      []



  `$: sortedlist = processList(list, options)`
  `$: selectAndRefresh(context)`

</script>



{#if list && list.length}
  {#each sortedlist as item, index}
    <ListHoverableContainer style="margin-bottom: 12px; padding: 4px 4px">
      <div slot="pre">
        <div is-favorite={item.is_favorite}>
          <ButtonIcon on:click={toggleFavorite(item)}>
            <IconToggleSvg bind:value={item.is_favorite} whenTrue="star" whenFalse="star-empty" />
          </ButtonIcon>       
        </div>     
      </div>
      <div>
        <FlexRow>
          <FlexRowCell flex="initial" > <!-- class:favorited={item.is_favorite} -->
          
          </FlexRowCell>            
          <FlexRowCell flex="initial">
            <ProgressPopover {item} bind:value={item.progress} on:update={updateRootItem} on:open on:close idAttribute="_id"/>
          </FlexRowCell>

          <FlexRowCell flex>
            <InlineInput bind:value={item.name} on:update={updateRootItem( { detail: { _id: item._id, name: item.name } })} on:clicked={goToItem(item)} let:label>
              <span ellipsis>
                <a href="{itemUrl}{item._id}" on:click|preventDefault><em>{label || "No Name"}</em></a>&nbsp;
              </span>
            </InlineInput>
          </FlexRowCell>

          <FlexRowCell flex="initial">
            <div layout="row">
              <div flex="initial" present={item.due_date != null}>
                <DueDatePopover {item} value={item.due_date} on:update={updateRootItem} idAttribute="_id" on:open on:close/>
              </div>

            </div>
          </FlexRowCell>
          <FlexRowCell flex="initial">
            

          </FlexRowCell>
        </FlexRow>
      </div>
      <div slot="menu">
        <ListMenu {item} on:select={updateRootItem} on:clone={cloneRootItem} on:destroy={destroy}/>
      </div>
    </ListHoverableContainer>
  {/each}
{/if}

<style lang="sass">

  [is-favorite="true"]
    svg
      path
        fill: blue  !important


  // legacy
  // global to ensure child menus inherit styles

  [type="list"]

    // border: 1px solid red //debug
    border-radius: 3px

    // width & position
    width: calc(100% + 64px)
    max-width: calc(100% + 64px)
    margin-left: -32px // 24+8px for icon cols

    margin-bottom: 8px

    [item-interior]
      width: 100%
      max-width: 100%

    [favorite], [menu], [progress] // set cell wdiths
      width: 32px

    [favorite]
      svg
        path
          fill: transparent


    [favorite].favorited
      svg
        path
          fill: var(--lightest-gray)

    [menu]
      text-align: center


    &:hover
      [favorite]
        svg
          path
            fill: var(--blue)

    box-sizing: border-box

    padding: 4px 0px

    

    .slot
      width: 100%
      max-width: 100%


    [show-on-hover]
      svg
        path
          fill: transparent

    &:hover
      background: #fcfcfc
      [show-on-hover]
        svg
          path
            fill: var(--gray)
        &:hover
          svg
            path
              fill: var(--blue)



</style>
