<script lang="coffeescript">

  import { createEventDispatcher } from "svelte"
  dispatch = createEventDispatcher()

  import {
    debug,
    IconSvg,
    IconToggleSvg,
    MenuContainer,
    MenuDivider,
    MenuItemWithIcon
  } from "../../../Braintapper"

  export options = 
    include_completed: true
    favorites_only: false
    sort_by: 'name'
    sort_descending: false
    context: "current"
  

  select = (item)->
    dispatch("selected",item)

</script>

<MenuContainer>

  <MenuItemWithIcon on:click={select("current")}>
    <span slot="icon"><IconToggleSvg value={options.context == "current"} whenTrue="check"/></span>
    Current
  </MenuItemWithIcon>
  <MenuItemWithIcon on:click={select("archived")}>
    <span slot="icon"><IconToggleSvg value={options.context == "archived"} whenTrue="check"/></span>
    Archived
  </MenuItemWithIcon>

  <MenuDivider/>

  <MenuItemWithIcon on:click={select("toggle_favorites")}>
    <span slot="icon"><IconToggleSvg value={options.favorites_only} whenTrue="check"/></span>
    Favorites Only
  </MenuItemWithIcon>
  <MenuItemWithIcon on:click={select("toggle_completed")}>
    <span slot="icon"><IconToggleSvg value={options.include_completed} whenTrue="check"/></span>
    Include Completed Items
  </MenuItemWithIcon>
  
  <MenuDivider/>

  <MenuItemWithIcon on:click={select("toggle_sort_by_name")}>
    <span slot="icon">
          {#if options.sort_by == 'name'}
            <IconToggleSvg value={options.sort_descending} whenTrue="arrow-down" whenFalse="arrow-up"/>
          {/if}
      </span>
    Sort By Name
  </MenuItemWithIcon>
  <MenuItemWithIcon on:click={select("toggle_sort_by_date")}>
    <span slot="icon">
          {#if options.sort_by == 'due_date'}
            <IconToggleSvg value={options.sort_descending} whenTrue="arrow-down" whenFalse="arrow-up"/>
          {/if}
      </span>
    Sort By Date
  </MenuItemWithIcon>


</MenuContainer>
