<script lang="coffeescript">

  import { onMount } from 'svelte'

  import {  debug,
            CssColors, 
            CssFonts, 
            CssResets, 
            # CssResponsive
            CssTypography,  CssDarkMode, Toast, LayoutLeftSplit, FavIconBadge } from './Braintapper'
  
  import { leftSplitContentElement,  SideNavigator, isSyncing, CssGlobal } from "./Application"

  
  window.debug = true

  debug.on = window.debug




  import Router from 'svelte-spa-router'

  
  import Pouch from "./Application/PouchDb/pouch_db.js"

  # Common

  ## Layout


  


  # Local


  


  # Routes

  import Radar from './Routes/Radar/Index.svelte'
  import Projects from './Routes/Lists/Index.svelte'
  import Project from './Routes/Lists/Show.svelte'
  import Settings from './Routes/Settings/Index.svelte'
  import Sync from './Routes/Sync/Index.svelte'
  import Help from './Routes/Help/Index.svelte'
  import FourOFour from './Routes/FourOFour.svelte'


  showToast = false
  toastMessage = ""

  routes =
    '/': Radar
    '/lists': Projects
    '/lists/:id': Project
    '/settings': Settings
    '/sync': Sync
    '/help': Help
    '*': FourOFour


  appName = "Braintapper Radar Lists"


  window.createPouchDb = ()->
    window.db = new PouchDB('braintapper-radar', {auto_compaction: true})
    window.db.createIndex
      index:
        fields: ["type"]
    window.pouch = new Pouch window.db

  window.createPouchDb()

  window.mounted = ()->
    window.pouch.sync_store = isSyncing
    window.pouch.notifier = toast
    window.pouch.sync()

  toast = (message)->
    showToast = true
    toastMessage = message

  syncCheck = ()->
    doSync = true
    if window.pouch.sync_db
      lastSyncAttempt = localStorage.getItem("radar_last_sync_attempt")
      if lastSyncAttempt?
          minutesSinceLastSync = Date.create().minutesSince(lastSyncAttempt)
          doSync = (minutesSinceLastSync >= 5)
      unless window.pouch.is_syncing
        if doSync
          window.pouch.sync()



  syncCheck.every(60000) # minute

  contentEl  = undefined


  

  routeEvent = (e)-> #  router must be mounted or won't work
    console.error "routeEvent"
    console.debug e

  toaster = (e)->



  onMount ()->
    leftSplitContentElement.set contentEl
    window.mounted()

  `$: show = showToast`

</script>


<svelte:head>
  <title>{ appName }</title>
</svelte:head>



<LayoutLeftSplit contentScrollable={true} on:toast={toaster}>
  <div slot="nav">
    <SideNavigator on:toast={toaster}/>
  </div>
  <div slot="default" style="padding: 48px 96px" bind:this={contentEl}>
    <Router {routes} on:routeEvent={routeEvent}/>
  </div>
</LayoutLeftSplit>
<Toast bind:show={showToast} message={toastMessage} delay={2}/>



