<script lang="coffeescript">
  import { onMount, createEventDispatcher } from "svelte"
  dispatch = createEventDispatcher()


  import {push, pop, replace, location} from 'svelte-spa-router'

  import { 

    debug, 
    ButtonIcon,
    IconSvg,
    IconToggleSvg,
    Indent, 
    FlexRow, 
    FlexRowCell,
    MenuContainer
  } from "../../Braintapper"

  import { 

          RootItemList,
          RootItem,
          PageContent,
          PageHeader,
          RadarMenu,
          ListHoverableContainer
          ProgressPopover,
          DueDatePopover,
          TimeHorizon } from "../../Application"

  debug.on = window.debug
  debug.context = "Radar/Index.svelte"

  

  

  import RadarItems from './_ListItems.svelte'

  






  getOrSet = (parameter, 
          override)->
    if localStorage.getItem(parameter)?
      return localStorage.getItem(parameter)  
    else
      localStorage.setItem parameter, override
      return override


  # testRouteEvent()

  #
  # filters
  #

  includeCompleted = getOrSet("radar_include_completed", true) == "true"
  ongoingOnly = getOrSet("radar_ongoing_only", false) == "true"
  horizon = getOrSet("radar_horizon", "14d")

  sortBy = getOrSet("radar_sort_by", "index")
  sortDescending = getOrSet("radar_sort_descending", false) == "true"


  options =
    include_completed: getOrSet("radar_include_completed", true) == "true"
    ongoing_only: getOrSet("radar_ongoing_only", false) == "true"
    sort_by: getOrSet("radar_sort_by", "index")
    sort_descending: getOrSet("radar_sort_descending", false) == "true"

  #
  # filter functions
  #
  
  updateHorizon = (e)->
    horizon = e.detail
    localStorage.setItem "radar_horizon", horizon



  handleRadarMenu = (e)->
    debug.log "handleRadarMenu::#{e.detail}"
    switch e.detail
      when "toggle_completed"
        includeCompleted = !includeCompleted
        options.include_completed = includeCompleted
        localStorage.setItem "radar_include_completed", includeCompleted
      when "toggle_ongoing"
        ongoingOnly = !ongoingOnly
        options.ongoing_only = ongoingOnly
        localStorage.setItem "radar_ongoing_only", ongoingOnly      
        
      when "toggle_sort_none"
        sortBy = "index"
        sortDescending = false
        options.sort_by = sortBy
        options.sort_descending = sortDescending
        localStorage.setItem "radar_sort_by", sortBy
        localStorage.setItem "radar_sort_descending", sortDescending
      when "toggle_sort_name"
        if sortBy == "name" # same as before
          sortDescending = !sortDescending
        else
          sortDescending = false
        sortBy = "name"
        
        options.sort_by = sortBy
        options.sort_descending = sortDescending
        localStorage.setItem "radar_sort_by", sortBy
        localStorage.setItem "radar_sort_descending", sortDescending
      when "toggle_sort_date"
        if sortBy == "due_date" # same as before
          sortDescending = !sortDescending
        else
          sortDescending = false      
        sortBy = "due_date"

        options.sort_by = sortBy
        options.sort_descending = sortDescending
        localStorage.setItem "radar_sort_by", sortBy
        localStorage.setItem "radar_sort_descending", sortDescending


  jump = (anchorName)->
    elem = u("[name=#{anchorName}]")
    scrollContainer = u("body > main > section").first()
    scrollContainer.scrollTop = elem.first().offsetTop - 40

  list = []

  resource = new RootItemList()

  getRootItems = ()->
    projectLoadedScb = (projects)->
      list = projects
      # console.log projects

    resource.select(projectLoadedScb, null, null)

  getRootItems()

  destroyListItem = (e)->




  export list = []

  style = ":hover { font-weight: bold }"

  updateAttribute = (e)->
    # Update Project Attribute

    found = list.find { _id: e.detail._id }

    debug.table "updateAttribute found", found, true
    debug.object "e.detail", e.detail

    if found?
      Object.merge found, e.detail
      resource.patch found.pouchify(), getRootItems

  goToItem = (p)->
    #console.log "gotoitem"
    #console.log "#/lists/#{p._id}"
    push "#/lists/#{p._id}"

  refresh = ()->
    dispatch "refresh"




  updateListItem = (e)-> # update a child in the list_items hierarchy
    change = e.detail

    console.error "updateListItem"
    console.log change

    change.list_id

    found = resource.items.find { _id: change.list_id }

    console.debug found

    originalItemReference = found.reference_from_ancestry(change.ancestry)
    originalItemIndex = originalItemReference.list_items.findIndex({uuid: change.uuid})
    originalItem = originalItemReference.list_items[originalItemIndex]


    # console.debug change

    Object.merge(originalItem, change)

    scb = (items)->


    console.error "CHANGED ITEM"
    console.debug found

    resource.patch found.pouchify(), getRootItems


    #refreshAndUpdate()




    

  radarList = (masterList, listHorizon, includeCompleted, showOngoingOnly)->
    # master list is ignored, it''s just there to trigger a change

    # root list should be sorted by name 

    clonedList = list.map (i)-> # make new clones of the list
      return new RootItem(i)

    
    output = []


    progressCondition = (item)->
      if includeCompleted
        return true
      else
        return !(item.progress.percent_complete == 100)

    ongoingCondition = (item)->
      if showOngoingOnly
        return item.is_ongoing
      else
        return true

    

    filterCondition = (item, index)->

      result = false
      if item.due_date?
        switch listHorizon
          when "0d"
            result = (Math.abs(Date.create(item.due_date).beginningOfDay().daysSince(Date.create().beginningOfDay())) == 0) && progressCondition(item) && ongoingCondition(item)
          when "1d","7d","14d"
            result = Math.abs(Date.create(item.due_date).beginningOfDay().daysSince(Date.create().beginningOfDay())) <= parseInt(listHorizon) && progressCondition(item) && ongoingCondition(item)
          when "1m","3m","6m","12m"
            result = Math.abs(Date.create(item.due_date).beginningOfDay().monthsSince(Date.create().beginningOfDay())) <= parseInt(listHorizon) && progressCondition(item) && ongoingCondition(item)
          when "1y"
            result = Math.abs(Date.create(item.due_date).beginningOfDay().yearsSince(Date.create().beginningOfDay())) <= parseInt(listHorizon) && progressCondition(item) && ongoingCondition(item)
          else
            result = true  && progressCondition(item) && ongoingCondition(item)
      else # due date is null
        if item.is_ongoing?
          if item.is_ongoing == true
            result = progressCondition(item) && ongoingCondition(item)
          
      item.list_items = item.list_items.clone().filter(filterCondition).sortBy(sortBy, sortDescending)
      result = result || (item.list_items.length > 0)

      return result
      

    output = clonedList.filter filterCondition
    

    
    return output.sortBy(sortBy, sortDescending)
    
  toggleExpanded = (list)->
    console.log "toggle Expanded #{list.name}"
    list.is_expanded_radar = !list.is_expanded_radar
    updateAttribute { detail: list }
  


  horizonRange = (range, includeCompletedFlag, ongoingOnlyFlag, sortByFlag, sortDescendingFlag)->


    itemsLabel = "in progress items"
    if includeCompletedFlag
      itemsLabel = "all items"

    ongoingSuffix = " as well as items flagged as ongoing"
    if ongoingOnlyFlag
      itemsLabel = "only items flagged as ongoing"
      ongoingSuffix = ""
    





    sortDescendingLabel = ""
    if sortDescendingFlag
      sortDescendingLabel = " (descending)"



    sortLabel = ""
    
    switch sortByFlag
      when "name"
        sortLabel = ", sorted by name#{sortDescendingLabel}"
      when "due_date"
        sortLabel = ", sorted by date#{sortDescendingLabel}"

      


    rangeLabel = ""

    switch range
      when "0d"
        rangeLabel = "for today"
      when "1d"
          rangeLabel = "for yesterday, today and tomorrow"
      when "1d","7d","14d"
        rangeLabel = " between #{Date.create().addDays(parseInt(horizon) * -1, true).medium()} and #{Date.create().addDays(parseInt(horizon) , true).medium()}"
        # Math.abs(Date.create().beginningOfDay().daysSince(Date.create().beginningOfDay())) 
      when "1m","3m","6m","12m"
        rangeLabel = " between #{Date.create().addMonths(parseInt(horizon) * -1, false).medium()} and #{Date.create().addMonths(parseInt(horizon) , false).medium()}"
      when "1y"
        rangeLabel = " between #{Date.create().addYears(parseInt(horizon) * -1, false).medium()} and #{Date.create().addYears(parseInt(horizon) , false).medium()}"
      else
        rangeLabel = "for all time"



    
    "Showing #{itemsLabel}#{ongoingSuffix} items #{rangeLabel}#{sortLabel}."






  `$: sortedList = radarList(list, horizon, includeCompleted, ongoingOnly, sortBy, sortDescending)`


</script>


<svelte:head>
  <title>Radar</title>
</svelte:head>

<PageContent>
  <PageHeader>
      <FlexRow>
        <FlexRowCell flex>
          <h1 title>Radar</h1>
        </FlexRowCell>
        <FlexRowCell style="padding-top: 16px">
          <RadarMenu bind:options={options} on:select={handleRadarMenu}/>
        </FlexRowCell>
      </FlexRow>  
  </PageHeader>

  <TimeHorizon  value={horizon} on:update={updateHorizon}/>
  

  <div horizon-label>
    <p>{ horizonRange(horizon, includeCompleted, ongoingOnly, sortBy, sortDescending) }</p>
    <p><i>Ongoing items without a date are always shown.</i></p>
    <p>Archived Lists are excluded from the Radar view.</p>
  </div>
  <section list>

    {#if sortedList.length > 0}
      {#each sortedList as list }

        <section radar>
          <!-- list -->
          <ListHoverableContainer>
              <div slot="pre">
                <div collapsible>
                  <ButtonIcon on:click={toggleExpanded(list)} style="height: 32px; width: 32px; left: -6px; position: relative">
                    <IconToggleSvg bind:value={list.is_expanded_radar} whenTrue="expanded" whenFalse="collapsed" size="32"/>
                  </ButtonIcon>
                </div>              
              </div>
              <div>
                <FlexRow classes="radar-list" style="line-height: 36px">


                  <FlexRowCell flex="initial" style="padding-right: 8px; padding-top: 3px">
                    <ProgressPopover item={list} bind:value={list.progress} on:update={updateAttribute} on:open on:close idAttribute="_id"/>
                  </FlexRowCell>

                  <FlexRowCell flex>
                    <div ellipsis>
                      <h3 ellipsis><a href="#/lists/{list._id}">{list.name || "No Name"}</a></h3>
                    </div>
                      

                  </FlexRowCell>

                  <FlexRowCell flex="initial">
                    <div layout="row">
                      <div flex="initial" present={list.due_date != null} visible-on-hover>
                        <DueDatePopover item={list} value={list.due_date} on:update={updateAttribute} on:open on:close idAttribute="_id"/>
                      </div>
                    </div>
                  </FlexRowCell>

                  
              </FlexRow>
            </div>
            <div slot="menu"><IconSvg/></div>
          </ListHoverableContainer>
          <hr/>

          <!-- list items -->

          {#if list.list_items.length > 0}
            {#if list.is_expanded_radar}
            <RadarItems bind:list={list} on:destroy={destroyListItem} on:update={updateListItem} listId={list._id}/>
            {/if}
          {:else}
            While the list has a date attached, no list items in this list with date assigned.
          {/if}


         

        </section>

      {/each}
    {:else}
      <section>
        <p>You currently have no lists.</p>
        <p>Go to the <a href="#/lists">Lists</a> view and assign some due dates to see some content here.</p>
      </section>
    {/if}


  </section>
</PageContent>
<style lang="sass">
  
  hr
    margin-bottom: 8px
    
  section[radar]
    margin-bottom: 36px

  ul[slot="nav"]
    margin-top: 100px

    padding-left: 32px
    a
      cursor: pointer
    li
      padding: 4px 0px

  header
    h1
      font-size: 36px
      color: var(--darkest-blue)
    margin-bottom: var(--spacing-m)


  [horizon-label]
    margin-top: 18px
    margin-bottom: 36px
    width: 100%
    p
      margin: 0px 24px
      color: var(--darker-gray)
      text-align: center

  // RADAR


  [collapsible], [menu], [progress] // set cell wdiths
    width: 32px


  .radar-list
    max-height: 42px



    //border: 1px solid red //debug
    border-radius: 3px

    // width & position
    width: calc(100% + 96px)
    max-width: calc(100% + 96px)
    margin-left: -64px // 24+8px for icon cols

    margin-bottom: 8px



    [menu]
      text-align: center


    box-sizing: border-box

    padding: 4px 0px

    

    .slot
      width: 100%
      max-width: 100%


    [visible-on-hover]
      svg
        path
          fill: transparent

    &:hover
      background: var(--list-row-highlight)
      [visible-on-hover]
        svg
          path
            fill: var(--gray)
        &:hover
          svg
            path
              fill: var(--blue)









</style>
