<script lang="coffeescript">

  import { onMount, createEventDispatcher } from "svelte"
  dispatch = createEventDispatcher()




  import { 
    debug,
    ButtonIcon,
    FlexRow,
    FlexRowCell,
    IconSvg,
    IconToggleSvg,
    Indent,
    InlineInput 
  } from "../../Braintapper"


  import { 
    ListItem, 
    ListItemMenu,  
    ListDraggableContainer,
    ListDroppableDivider,
    ProgressPopover, 
    DueDatePopover 
    } from "../../Application"



  
  export list = []
  export dragState = undefined
  

  message = ""

  style = ":hover {font-weight: bold}"

  goToItem = (childItem)->
    location.href="#/lists/#{childItem.id}"

  updateListItem = (e)->
    dispatch "update", e.detail

  cloneListItem = (e)->
    dispatch "clone", e.detail

  destroyDispatcher = (e)->
    dispatch "destroy", e.detail
    showDeleteConfirmation = false

  # drag and drop

  dropDispatcher = (e)->
    dispatch "drop", e.detail


  nestDispatcher = (e)->
    dispatch "nest", e.detail

  addDispatcher = (e)->
    console.log "addDispatcher"
    dispatch "add", e.detail


  onDrag = (e)->
    dispatch "dragging", true

  onDragStop = (e)->
    console.log e
    console.log "dragStop"
    dispatch "dragStop", true



  toggleExpanded = (item)->
    item.is_expanded = !item.is_expanded
    dispatch "update", {uuid: item.uuid, is_expanded: item.is_expanded, ancestry: item.ancestry }


</script>
{#if (list.type != "RootItem") && (list.list_items.length > 0)}
  <ListDroppableDivider item={{ ancestry: list.ancestry.clone().append(list.uuid), index: -1} } indentation={list.ancestry.length + 1}  on:drop={dropDispatcher} on:nest={nestDispatcher} firstItem={true} on:add={addDispatcher} index={-1} bind:dragState={dragState}/>
{/if}
{#each list.list_items as listItem, index}


  <ListDraggableContainer item={listItem} index={index} indent={listItem.ancestry.length} on:nest={nestDispatcher} on:update={updateListItem} on:dragging={onDrag} on:dragStop={onDragStop}>
      <div type="list-item">
        <FlexRow >
          
            <FlexRowCell flex="initial" style="width: {listItem.ancestry.length * 24}px">
              <Indent level={listItem.ancestry.length}/>
            </FlexRowCell>
            
            <FlexRowCell flex="initial" style="width: 24px">
              {#if listItem.list_items.length > 0}
                <ButtonIcon on:click={toggleExpanded(listItem)}>
                  <IconToggleSvg bind:value={listItem.is_expanded} whenTrue="expanded" whenFalse="collapsed"/>
                </ButtonIcon>
              {/if}
            </FlexRowCell>

          <FlexRowCell flex="initial"> 
            <ProgressPopover item={listItem} bind:value={listItem.progress} on:update={updateListItem} on:open on:close idAttribute="uuid" style="padding-right: 8px"/>
          </FlexRowCell>

          <FlexRowCell flex style="width: 100px">
            <div ellipsis>
              <InlineInput bind:value={listItem.name} on:update={updateListItem({ detail: { uuid: listItem.uuid, ancestry: listItem.ancestry, name: listItem.name  } })}  let:label>
                {#if listItem.is_hidden}<span ellipsis>Text for this is hidden. You can edit it to see it, or unhide it using the menu.</span>
                {:else}<span ellipsis>{label || "No Name"}</span>&nbsp;
                  {#if listItem.is_ongoing}
                    <span><IconSvg icon="ongoing" size="12"/></span>
                  {/if}
                {/if}
              </InlineInput>
            </div>

          </FlexRowCell>
          <FlexRowCell flex="initial">
            <FlexRow>
              <!-- due date menu -->
              <FlexRowCell flex="initial">
                <DueDatePopover item={listItem} value={listItem.due_date} on:update={updateListItem} on:open on:close idAttribute="uuid"/>
              </FlexRowCell>
            </FlexRow>
          </FlexRowCell>

        </FlexRow>
      </div>
      <div slot="menu">
        <ListItemMenu bind:item={listItem} on:update={updateListItem} on:clone={cloneListItem} on:destroy={destroyDispatcher}/>
      </div>
    
  </ListDraggableContainer>
  {#if (listItem.list_items.length == 0) || (!listItem.is_expanded)}
  <ListDroppableDivider item={{ ancestry: listItem.ancestry.clone().append(listItem.uuid), list_items: [], index: -1}} indentation={listItem.ancestry.length + 1} on:drop={dropDispatcher} on:add={addDispatcher}  bind:dragState={dragState}/>
   <div style="height: 4px;"/>
  {/if}
  <!-- children -->
  {#if listItem.is_expanded }
    <svelte:self bind:list={listItem}  bind:dragState={dragState} on:update={updateListItem} on:drop={dropDispatcher} on:clone={cloneListItem} on:nest={nestDispatcher} on:destroy={destroyDispatcher} on:add={addDispatcher} on:dragging={onDrag} on:dragStop={onDragStop}/>
  {/if}
  <!-- don't show when last item of root -->
  {#if !((list.type == "RootItem") && (index == (list.list_items.length - 1)))}
    <ListDroppableDivider item={listItem} indentation={listItem.ancestry.length} on:drop={dropDispatcher} on:nest={nestDispatcher} on:add={addDispatcher}  bind:dragState={dragState}/>
    <div style="height: 4px;"/>
  {/if}


{/each}


<style lang="sass">
  [type="list-item"]
    width: 100%
    max-width: 100%

    [main]
      width: 100px // this is a dummy value to make the ellipsis work
    &:hover
      [present]
        display: flex

</style>
