<script lang="coffeescript">
  import { createEventDispatcher } from "svelte"
  dispatch = createEventDispatcher()


  import Menu from './_Radar.svelte'
  

  import { 
    ButtonIconPopover,
    IconSvg
  } from '../../../Braintapper'  

  
  export options = undefined


  handleMenuSelection = (selection)->
    dispatch "select", selection

  popoverEvents = 
    "selected": (e)->
      handleMenuSelection e.detail



</script>

<ButtonIconPopover popover={Menu} events={popoverEvents} bind:options>
  <IconSvg classes="menu" icon="menu"/>
</ButtonIconPopover>
